import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Child from "./Child"
import Player from "./player"
import login from "./login";
import JsonRead from "./jsonread"
import selectvideos from "./selectVideos";
import moveLinkBoxer from "./boxercise";
import moveLinkHiit from "./hiit";
import moveLinkYoga from "./yoga";
import moveLinkExercise from "./exercise";
import result from "./result";

class App extends React.Component {
  render() {
    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          {/* <Route exact path="/" component={JsonRead} /> */}
          <Route exact path="/" component={login} />
          <Route exact path="/exercise" component={moveLinkExercise} />
          <Route exact path="/result" component={result} />
        </Switch>
      </BrowserRouter>
    );
  }
}

