import React from "react";

const calcHu = (mData) => {
    const calcMoment = (i,j) => {
        let Mij = 0
        for (let n in mData[0]){
            Mij += (mData[0][n] ** i) * (mData[1][n] ** j)
        }
        return Mij
    }

    const calcCentorMoment = (i,j) => {
        let mij = 0
        let x_ = calcMoment(1,0)/calcMoment(0,0)
        let y_ = calcMoment(0,1)/calcMoment(0,0)
        for (let n in mData[0]){
            mij += ((mData[0][n]-x_)**i) * ((mData[1][n]-y_)**j)
        }
        return mij
    }

    const calcNyu = (i,j) => {
        let nij = calcCentorMoment(i,j) / (calcCentorMoment(0,0) ** (1+((i+j)/2)))
        return nij
    }

    let I1 = calcNyu(2,0) + calcNyu(0,2)
    let I2 = ((calcNyu(2,0) - calcNyu(0,2))**2) + 4*(calcNyu(1,1)**2)
    let I3 = ((calcNyu(3,0) - 3*calcNyu(1,2))**2) + ((3*calcNyu(2,1) - calcNyu(0,3))**2)
    let I4 = ((calcNyu(3,0) + calcNyu(1,2))**2) + ((calcNyu(2,1) + calcNyu(0,3))**2)
    let I5 = (calcNyu(3,0) - 3*calcNyu(1,2)) * (calcNyu(3,0)+calcNyu(1,2)) * (((calcNyu(3,0)+calcNyu(1,2))**2)-3*((calcNyu(2,1)+calcNyu(0,3))**2)) + (3*calcNyu(2,1)-calcNyu(0,3)) * (calcNyu(2,1)+calcNyu(0,3)) * (3*((calcNyu(3,0)+calcNyu(1,2))**2)-((calcNyu(2,1)+calcNyu(0,3))**2))
    let I6 = (calcNyu(2,0)-calcNyu(0,2)) * (((calcNyu(3,0)+calcNyu(1,2))**2) - ((calcNyu(2,1)+calcNyu(0,3))**2)) + 4*calcNyu(1,1)*(calcNyu(3,0)+calcNyu(1,2))*(calcNyu(2,1)+calcNyu(0,3))
    let I7 = (3*calcNyu(2,1)-calcNyu(0,3)) * (calcNyu(3,0)+calcNyu(1,2)) * (((calcNyu(3,0)+calcNyu(1,2))**2)-3*((calcNyu(2,1)+calcNyu(0,3))**2)) - (calcNyu(3,0)-3*calcNyu(1,2))*(calcNyu(2,1)+calcNyu(0,3)) * (3*((calcNyu(3,0)+calcNyu(1,2))**2)-((calcNyu(2,1)+calcNyu(0,3))**2))
    return [I1,I2,I3,I4,I5,I6,I7]
}

export default calcHu